@import url(https://fonts.googleapis.com/css?family=Seaweed+Script&display=swap);
.header{z-index:1;font-family:'Seaweed Script', cursive;display:flex;justify-content:center;height:70px;background:#FFFFFF;box-shadow:0px 4px 4px rgba(0,0,0,0.25);position:fixed;width:100%}.header__h1{font-style:normal;font-weight:normal;font-size:30px;line-height:41px;letter-spacing:0.51em;color:#FF8A00;margin-top:13px 0}.header__logo{height:50px;width:50px;margin-top:8px}

.menu{width:250px;height:100vh;border:1px solid #D0D0D0;position:fixed}.form{padding-top:100px;font-family:'Roboto', sans-serif;font-style:normal;font-weight:normal;line-height:19px;text-align:left}.form .submit{margin:0 40px}input[type=checkbox]{display:none;margin:.5em;padding:0.2em}input[type=checkbox]+label{display:block;margin-left:40px;cursor:pointer;color:#818181}input[type=checkbox]+label:before{content:"\2714";border-radius:4px;display:inline-block;border:0.1em solid #3058913d;width:20px;height:20px;font-size:16px;box-sizing:border-box;padding:0.1px 1.3px 1.3px;margin-right:10px;vertical-align:bottom;color:transparent;-webkit-transition:.2s;transition:.2s}input[type=checkbox]+label:active:before{-webkit-transform:scale(0.5);transform:scale(0.5)}input[type=checkbox]:checked+label:before{background-color:white;border-color:#818181;color:#818181}input[type=checkbox]:disabled+label:before{-webkit-transform:scale(1);transform:scale(1);border-color:#aaa}input[type=checkbox]:checked:disabled+label:before{-webkit-transform:scale(1);transform:scale(1);background-color:#8da7fc;border-color:#bfb}

.menu{width:250px;height:100vh;border:1px solid #D0D0D0;position:fixed}.form{padding-top:100px;font-family:'Roboto', sans-serif;font-style:normal;font-weight:normal;line-height:19px;text-align:left}.form .submit{margin:0 40px}input[type=checkbox]{display:none;margin:.5em;padding:0.2em}input[type=checkbox]+label{display:block;margin-left:40px;cursor:pointer;color:#818181}input[type=checkbox]+label:before{content:"\2714";border-radius:4px;display:inline-block;border:0.1em solid #3058913d;width:20px;height:20px;font-size:16px;box-sizing:border-box;padding:0.1px 1.3px 1.3px;margin-right:10px;vertical-align:bottom;color:transparent;-webkit-transition:.2s;transition:.2s}input[type=checkbox]+label:active:before{-webkit-transform:scale(0.5);transform:scale(0.5)}input[type=checkbox]:checked+label:before{background-color:white;border-color:#818181;color:#818181}input[type=checkbox]:disabled+label:before{-webkit-transform:scale(1);transform:scale(1);border-color:#aaa}input[type=checkbox]:checked:disabled+label:before{-webkit-transform:scale(1);transform:scale(1);background-color:#8da7fc;border-color:#bfb}

.cocktail{width:150px;position:relative;bottom:0;margin:0 7px 20px 0;box-sizing:border-box;-webkit-transition:color 0.3s, -webkit-transform 0.3s ease 0s;transition:color 0.3s, -webkit-transform 0.3s ease 0s;transition:transform 0.3s ease 0s, color 0.3s;transition:transform 0.3s ease 0s, color 0.3s, -webkit-transform 0.3s ease 0s}.cocktail__img{width:150px;height:150px}.cocktail__title{margin-top:20px;display:block;font-style:normal;font-weight:normal;font-size:18px;line-height:21px}.cocktail:hover{-webkit-transform:scale(1.03, 1.03);transform:scale(1.03, 1.03);color:#818181;cursor:pointer}

.content-wrap{padding:70px 50px 0;width:100%;margin-left:250px}.content{display:flex;flex-wrap:wrap;justify-content:space-between;margin:auto}.content__h2{display:block;text-align:left;font-family:'Roboto', sans-serif;font-style:normal;font-weight:normal;line-height:19px;text-align:left;color:#818181;margin:50px 0 35px;border-bottom:1px solid #D0D0D0;padding-bottom:10px}@media (max-width: 700px){.content{justify-content:center}.content-wrap{margin:0}.menu{display:none}}@media (max-width: 370px){.header__h1{font-size:22px}}

.App{text-align:center;font-family:'Roboto', sans-serif}.header__img{width:100%}.flex-wrap{display:flex}

